.button-related {
    width: fit-content;
    min-width: fit-content;
    min-height: 32px;
    padding: 7px 12px;
    border-radius: 24px;
    background-color: #f0f0f1;
    color: #4a4b57;
    font-size: 14px;
    white-space: nowrap;
    margin: 0 2px;

    &:hover {
        cursor: pointer;
        box-shadow: 0 2px 4px #b4b4bb, 0 4px 8px #d2d2d6;

    }

    &.active {
        color: #fff;
        background-color: #5a5b6a;

        &:hover {
            cursor: default;
            box-shadow: none;
        }
    }
}