.button-category {

    padding: 10px 20px 8px 12px;
    border-radius: 50px;
    border: 1px solid #9dc2ff;
    width: fit-content;
    height: 36px;
    box-sizing: initial;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s linear;

    &:hover {
        background-color: #ebf2ff;
    }

    svg {
        width: 24px;
        height: 24px;
        fill: #2264d1;
        margin-right: 8px;
    }

    span {
        color: #2264d1;
        font-weight: 400;
        font-size: 24px;
        letter-spacing: 0.75px;
    }

    &.background {
        border: 1px solid #ebf2ff;
        background-color: #ebf2ff;
        
        &:hover {
            box-shadow: var(--second-box-shadow)
        }
    }
}