.category-list {
    .category-list_header {
        margin-bottom: 16px;

        .category-list_header-button {
            width: 100%;
            display: flex;
            align-items: center;
            padding: 10px 20px 10px 12px;
            background-color: #ebf2ff;
            border-radius: 50px;
            color: #2264d1;
            transition: all 0.3s linear;
           

            svg {
                width: 24px;
                height: 24px;

                margin-right: 8px;
                fill: #2264d1;
            }

            span {
                font-size: 24px;
                font-weight: 500;
                line-height: 36px;
                letter-spacing: 0.45px;
                color: #2264d1;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            &:hover {
                cursor: pointer;
                box-shadow: 0 2px 4px #9dc2ff,0 4px 8px #c4daff;
            }
        }
       


    }

    .category-list_body {
        margin-left: 16px;

        .category-list_item {
            display: flex;
            align-items: center;
            height: 40px;
            padding: 8px 12px; 
            max-width: 100%;
            width: fit-content;
           

            span {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                max-width: 100%;
            }

            &:hover {
                cursor: pointer;
                box-shadow: 0 2px 4px #d2d2d6, 0 4px 8px #b4b4bb;
            }
        }
    }
}