:root {
    --main-bg: #ffffff;
    --second-bg: #fafafb;
    --txt-color: #455560;
    --txt-white: #fff;
    --main-color:  #2264d1;
    --second-color: #9dc2ff;
    --box-shadow: 0 1px 2px #9dc2ff,0 2px 4px #c4daff;
    --second-box-shadow: 0 2px 4px #9dc2ff,0 4px 8px #c4daff;
    --third-box-shadow: 0 4px 8px #9dc2ff,0 8px 16px #c4daff;

    --main-bg-light: #ffffff;
    --second-bg-light: #fafafb;
    --txt-color-light: #455560;
    --box-shadow-light: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    --main-bg-dark: #2d2d2d;
    --second-bg-dark: #202020;
    --txt-color-dark: #bbbbbb;
    --box-shadow-dark: rgba(0, 0, 0, 0.2) 0px 5px 10px;

    --main-color-blue: #349eff;
    --second-color-blue: #62b4ff;
    --box-shadow-blue: 0 1px 2px #9dc2ff,0 2px 4px #c4daff;

    --main-color-red: #fb0b12;
    --second-color-red: #ff4a6b;

    --main-color-cyan: #10d4d2;
    --second-color-cyan: #2ae9e6;

    --main-color-green: #019707;
    --second-color-green: #4caf50;

    --main-color-orange: #d68102;
    --second-color-orange: #fca11a;

    --transition-cubic: cubic-bezier(0.175, 0.885, 0.32, 1.275);

    /* Header */
    --height-header: 64px;
    --height-header-small: 48px;
    --height-header-mobile: 100px;
    /* Filter */
    --width-filter: 320px;

    /* Utils */
    --height-utils: 48px;

}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-family: 'Roboto', sans-serif;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    position: absolute;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #9dc2ff;
    border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #78a4ed;
    cursor: pointer;
}