.button-filter {
    padding: 8px 12px;
    width: fit-content;
    height: fit-content;
    display: flex;
    align-items: center;
    border-radius: 4px;
    cursor: pointer;

    span {
        color: #2264d1;
        text-transform: uppercase;
        white-space: nowrap;
        letter-spacing: 0.75px;
        font-size: 14px;
        font-weight: 500;
        margin-right: 10px;
    }

    svg {
        fill: #2264d1;
        width: 24px;
        height: 24px;
    }

    &:hover {
        box-shadow: 0 2px 4px #9dc2ff,0 4px 8px #c4daff;
    }
}