.combo-box {
    position: relative;
    width: 204px;
    height: 36.85px;
    border-radius: 4px;
    background-color: #fff;

    .combo-box_parent {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        padding: 0 12px;
        border-radius: 4px;
        border: 1px solid #b4b4bb;
        cursor: pointer;
        transition: all 0.24s linear;

        &:hover {
            border-color: #2264d1;

            &.label {
                .combo-box_parent-label {
                    color: #2264d1;
                }
            }
        }

        .combo-box_parent-label {
            display: none;
        }

        &.label {
            .combo-box_parent-label {
                display: block;
                position: absolute;
                top: 0;
                transform: translateY(-50%);
                background: #fff;
                text-transform: uppercase;
                font-size: 10px;
                color: #787885;
                font-weight: 500;
                padding: 0 4px;
                line-height: 12px;
            }
        }

        &.selected {
            border-color: #000;

            .combo-box_parent-placeholder {
                color: #000;
            }
        }

        .combo-box_parent-placeholder {
            color: #b4b4bb;
            font-size: 16px;
            font-weight: 400;
        }

        .combo-box_parent-icon svg {
            width: 24px;
            height: 24px;
            fill: #787885;
        }
    }

    .combo-box_children {
        display: none;
        position: absolute;
        top: -4px;
        left: -4px;
        font-size: 16px;
        max-height: 256px;
        min-width: calc(100% + 8px);
        background-color: #fff;
        border-radius: 12px;
        box-shadow: 0 4px 8px rgb(0 0 0 / 20%), 0 8px 16px rgb(0 0 0 / 20%);
        z-index: 5;
        opacity: 0;
        transform-origin: 50% 24px 0px;
        transform: scaleY(0);
        transition: all 0.3s linear;

        
        .combo-box_item {
            height: 48px;
            padding: 0 12px;
            display: flex;
            align-items: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            user-select: none;
            line-height: 0.44px;
            font-size: 16px;

            &:first-child {
                border-top-left-radius: 12px;
                border-top-right-radius: 12px;
            }

            &:last-child {
                border-bottom-left-radius: 12px;
                border-bottom-right-radius: 12px;
            }

            &:hover {
                cursor: pointer;
                color: #fff;
                background-image: linear-gradient(90deg,#5a5b6a,#9696a0);
            }

            &.active {
                cursor: default;
                color: #fff;
                background-image: linear-gradient(90deg,#5a5b6a,#9696a0);
            }
        }
    }

    &.open {
       
        .combo-box_children {
            display: block;
            opacity: 1;
            transform: scaleY(1);
        }
    }
}