.checkbox {
    width: 100%;
    height: 40px;
    min-height: 40px;
    min-width: 272px;
    padding: 6px 12px;
    border-radius: 4px;
    margin-bottom: 4px;
    cursor: pointer;

    display: flex;
    align-items: center;
    transition: all 0.3s linear;

    .checkbox_box {
        width: 34px;
        height: 34px;
        border-radius: 50%;
        margin-right: 12px;
        display: flex;
        align-items: center;
        justify-content: center;

        .checkbox_box-square {
            width: 18px;
            height: 18px;
            border: 2px solid #2264d1;
            border-radius: 3px;

            display: flex;
            align-items: center;
            justify-content: center;

            svg {
                display: none;
                fill: #000;
            }
        }

        
    }

    .checkbox_content {
        font-size: 14px;
        font-weight: 400;
        color: #000;
    }

    &:hover {
        background: #fff;
        box-shadow: 0 2px 4px #9dc2ff,0 4px 8px #c4daff;

        .checkbox_box {
            background-color: #ebf2ff;
        }
    }

    &.active {

        background-color: #d8e6ff;

        .checkbox_box { 

            .checkbox_box-square {
                background-color: #2264d1;

                svg {
                    display: block;
                    fill: #fff;
                }
            }
        }

        &:hover {
            background-color: #d8e6ff;
            box-shadow: none;

            .checkbox_box {
                background: none;
            }
        }
    }
    
}