.util {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    background-color: #fff;

    .util_left {
        width: 32px;
        height: 32px;
        position: relative;

        &:hover {
            .util_left-tooltip {
                display: block
            }
        }

        img {
            width: 100%;
            height: 100%;
            cursor: pointer;
            opacity: 0.9;
            transition: all 0.3s linear;

            &:hover {
                opacity: 1;
            }
        }

        .util_left-tooltip {
            display: none;
            position: absolute;
            bottom: calc(100% + 8px);
            left: 0%;
            padding: 8px;
            font-size: 12px;
            font-weight: normal;
            color: #fff;
            background-color: #999;
            border-radius: 4px;
            white-space: nowrap;
            width: fit-content;
            z-index: 999;
        }
    }

    .util_center {
        display: flex;
        align-items: center;

        .util_center-item {
            margin-left: 8px;
        }
    }

    .util_right {

        .util_right-btn {
            padding: 10px 16px;
            font-size: 14px;
            box-shadow: 0 2px 4px #9dc2ff,0 4px 8px #c4daff;
            color: #fff;
            background-color: #2979ff;
            transition: all 0.3s linear;
            border-radius: 4px;
            cursor: pointer;
            font-weight: 500;
            text-transform: uppercase;
            white-space: nowrap;
            letter-spacing: 0.75px;

            &:hover {
                background-color: #2264d1;
                box-shadow: 0 4px 8px #9dc2ff,0 8px 16px #c4daff;
            }
        }
    }
}

@media (max-width: 1024px) {  
    .util {

        .util_center {
            display: none;
        }
    }
}